/* socket io demo styles  */

/* This is global but... good */
input, button {
  outline: none;
}

#sio-demo-content {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font: 13px Helvetica, Arial;
}

.sio-msg-box {
  word-wrap: break-word;
}

#sio-messages {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

#sio-messages li {
  padding: .577rem .654rem;
  border-top: 3px solid #efefef;
}

#notifyUser {
  position: absolute;
  right: 2.4rem;
  bottom: 5rem;
  background-color:rgba(0, 0, 0, 0);
}

.odd-row {
  background-color: #CCC;
}

.even-row {
  background-color: #EEE;
}

.sio-form {
  margin-bottom: 0;
  background-color: #f9f9f9;
  padding: .731rem;
  border: 1px solid #C0C0C0;
}

.sio-content {
  background-color: white;
  border: 1px solid #C0C0C0;
  border-bottom: 0;
  position: absolute;
  top: 4rem;
  bottom: 5rem;
  left: 0px;
  right: 0px;
  overflow: auto;
  margin-left: 1.3333rem;
  margin-right: 1.3333rem;
}

.sio-footer {
  position:absolute;
  bottom:0px;
  height:3.6667rem;
  left:0px;
  right:0px;
  overflow:hidden;
  margin-left: 1.3333rem;
  margin-right: 1.3333rem;
  margin-bottom: 1.3333rem;
}

.pxh-card header#sio-header {
  border-bottom: 0;
  margin-bottom: 0;
}

.sio-header {
  top:0px;
  left:0px;
  height:4rem;
  right:0px;
  overflow:hidden;
}

#sio-msg-input-box {
  border-radius: 2px 0 0 2px;
}

#sio-send-msg-btn {
  border-radius: 0 2px 2px 0;
}

#sio-connect-btn {
  width: 102px;
}

.sio {
  position: relative;
  height: calc(100vh - 76.666px);
  min-height: 264px;
}


