////
/// APP.SCSS
/// Defines the Sass manifest for ui-microapp, which compiles to app.css.
/// @group ui-microapp
////

/*! ui-microapp 1.0.0 */
/* app.css */

// ** #SETTINGS ** //

@import "_settings.px-config.scss";

@import "px-colors-design/_settings.colors.scss";

@import "inuit-responsive-settings/_settings.responsive.scss";

@import "px-widths-tools-design/_tools.widths.scss";




// ** #TOOLS ** //

@import "inuit-functions/_tools.functions.scss";

@import "px-functions-design/_tools.functions.scss";

@import "px-mixins-design/_tools.mixins.scss";

@import "inuit-responsive-tools/_tools.responsive.scss";




// ** #GENERIC ** //

// @import "px-normalize-design/_generic.normalize.scss";

// @import "px-box-sizing-design/_generic.box-sizing.scss";

@import "px-helpers-design/_generic.helpers.scss";




// ** #BASE ** //

@import "px-viewport-design/_base.viewport.scss";

@import "px-flexbox-design/_base.flexbox.scss";

// px-typography-design is not required as we're already
// including the GE Inspira font at the App Hub level via pxh-chrome
// @import "px-typography-design/_base.typography.scss";
// stripped-down styles from px-typography-design that
// we want to import:

@import "_base.typography.scss";

@import "px-code-design/_base.code.scss";

@import "px-headings-design/_base.headings.scss";

// px-iconography-design is not required as we're already
// including Font Awesome at the App Hub level via pxh-chrome
// @import "px-iconography-design/_base.iconography.scss";
// stripped-down styles from px-iconography-design that
// we want to import:

@import "_base.iconography.scss";

@import "px-tables-design/_base.tables.scss";




// ** #OBJECTS ** //

@import "px-list-bare-design/_objects.list-bare.scss";

@import "px-list-inline-design/_objects.list-inline.scss";

@import "px-list-ui-design/_objects.list-ui.scss";

// px-button-group-design imports px-buttons-design,
// so if we're already importing px-button-group-design
// we don't need to import px-buttons-design
// @import "px-buttons-design/_objects.buttons.scss";

$fa-font-path: '../bower_components/font-awesome/fonts';
@import "px-button-group-design/_objects.button-group.scss";
$fa-font-path: null;

@import "px-layout-design/_objects.layout.scss";

@import "px-box-design/_objects.box.scss";

@import "px-flag-design/_objects.flag.scss";

@import "px-toggle-design/_objects.toggle.scss";

// px-input-group-design imports px-forms-design,
// so if we're already importing px-input-group-design
// we don't need to import px-forms-design
// @import "px-forms-design/_base.forms.scss";

@import "px-input-group-design/_objects.input-group.scss";




// ** #APPLICATION ** //

@import "_pxh-card.scss";

@import '../modules/one/styles/_module.scss';

@import '../modules/one/styles/_example-socket-io.scss';




// ** #TRUMPS ** //

// px-spacing-responsive-design imports px-spacing-design,
// so if we're already importing px-spacing-responsive-design
// we don't need to import px-spacing-design
// @import "px-spacing-design/_trumps.spacing.scss";

@import "px-spacing-responsive-design/_trumps.spacing-responsive.scss";

// px-widths-responsive-design imports px-widths-design,
// so if we're already importing px-widths-responsive-design
// we don't need to import px-widths-design
// @import "px-widths-design/_trumps.widths.scss";

@import "px-widths-responsive-design/_trumps.widths-responsive.scss";

@import "inuit-clearfix/_trumps.clearfix.scss";




// ** #OVERRIDES ** //

// hack to fix flag class when applied to <figure> elements
.flag {
  margin-left: 0;
  margin-right: 0;
}

// hack to override embedded polymer apps with misplaced .viewport classes
.viewport .viewport {
  margin-left: 0;
  margin-right: 0;
}
