////
/// _SETTINGS.PX-CONFIG.SCSS
/// Declares configuration flags for turning on Px/Predix UI/inuitcss classes.
/// @group px
////

// required since we're not including px-box-sizing-design but we are using the border-box model courtesy of pxh-chrome
$inuit-global-border-box  : true;




// ** #SETTINGS ** //




// ** #TOOLS ** //




// ** #GENERIC ** //




// ** #BASE ** //

// PX-VIEWPORT-DESIGN
// configuration variables
// documented in the px-viewport-design README.md:
// https://github.com/predixdev/px-viewport-design

/// @type Length [default] 100% - Width of viewport at desk-wide breakpoint
$desk-wide-width: 100%;

/// @type Length [default] 100% - Maximum width of viewport at desk-wide breakpoint
$desk-wide-max-width: 100%;

/// @type Length [default] auto - Default margin to apply around viewport
$viewport-margin: 0;




// PX-CODE-DESIGN
// configuration variables for px-code-design/_base.code.scss
// documented in the px-code-design README.md:
// https://github.com/predixdev/px-code-design

/// @type Boolean [default] false - Produce CSS for inline code snippets?
$inuit-enable-code--inline  : true;




// PX-TABLES-DESIGN
// configuration variables for px-tables-design/_base.tables.scss
// documented in the px-tables-design README.md:
// https://github.com/predixdev/px-tables-design

/// @type Boolean [default] false - Produce CSS for tables with a fixed width?
$inuit-enable-table--fixed      : true;

/// @type Boolean [default] false - Produce CSS for tables cells with tiny padding?
$inuit-enable-table--small      : true;

/// @type Boolean [default] false - Produce CSS for tables cells with small padding?
$inuit-enable-table--large      : true;

/// @type Boolean [default] false - Produce CSS for borders around all cells?
$inuit-enable-table--no-cells   : true;

/// @type Boolean [default] false - Produce CSS for borders only table rows?
$inuit-enable-table--rows       : true;

/// @type Boolean [default] false - Produce CSS for borders only table columns?
$inuit-enable-table--columns    : true;




// PX-LIST-INLINE-DESIGN
// configuration variables for px-list-inline-design/_objects.list-inline.scss
// documented in the px-list-inline-design README.md:
// https://github.com/predixdev/px-list-inline-design

/// @type Boolean [default] false - Produce CSS for character to delimit list items?
$inuit-enable-list-inline--delimited  : true;




// PX-LIST-UI-DESIGN
// configuration variables for px-list-ui-design/_objects.list-ui.scss
// documented in the px-list-ui-design README.md:
// https://github.com/predixdev/px-list-ui-design

/// @type Boolean [default] false - Produce CSS for list-uis with tiny padding?
$inuit-enable-list-ui--tiny     : true;

/// @type Boolean [default] false - Produce CSS for list-uis with small padding?
$inuit-enable-list-ui--small    : true;

/// @type Boolean [default] false - Produce CSS for list-uis with large padding?
$inuit-enable-list-ui--large    : true;

/// @type Boolean [default] false - Produce CSS for list-uis with huge padding?
$inuit-enable-list-ui--huge     : true;




// PX-BUTTON-GROUP-DESIGN
// configuration variables for px-button-group-design/_objects.button-group.scss
// documented in the px-button-group-design README.md:
// https://github.com/predixdev/px-button-group-design

/// @type Boolean [default] false - Produce CSS for primary buttons?
$inuit-enable-btn--primary                          : true;

/// @type Boolean [default] false - Produce CSS for tertiary buttons?
$inuit-enable-btn--tertiary                         : true;

/// @type Boolean [default] false - Produce CSS for small buttons?
$inuit-enable-btn--small                            : true;

/// @type Boolean [default] false - Produce CSS for large buttons?
$inuit-enable-btn--large                            : true;

/// @type Boolean [default] false - Produce CSS for huge buttons?
$inuit-enable-btn--huge                             : true;

/// @type Boolean [default] false - Produce CSS for full buttons?
$inuit-enable-btn--full                             : true;

/// @type Boolean [default] false - Produce CSS for icon buttons?
$inuit-enable-btn--icon                             : true;

/// @type Boolean [default] false - Produce CSS for bare buttons?
$inuit-enable-btn--bare                             : true;

/// @type Boolean [default] false - Produce CSS for bare--primary buttons?
$inuit-enable-btn--bare--primary                    : true;

/// @type Boolean [default] false - Produce CSS for disabled buttons?
$inuit-enable-btn--disabled                         : true;




// PX-LAYOUT-DESIGN
// configuration variables for px-layout-design/_objects.layout.scss
// documented in the px-layout-design README.md:
// https://github.com/predixdev/px-layout-design

/// @type Boolean [default] false - Produce CSS for layout with tiny gutters between layout items?
$inuit-enable-layout--tiny          : true;

/// @type Boolean [default] false - Produce CSS for layout with small gutters between layout items?
$inuit-enable-layout--small         : true;

/// @type Boolean [default] false - Produce CSS for layout with large gutters between layout items?
$inuit-enable-layout--large         : true;

/// @type Boolean [default] false - Produce CSS for layout with huge gutters between layout items?
$inuit-enable-layout--huge          : true;

/// @type Boolean [default] false - Produce CSS for layout with no gutters between layout items?
$inuit-enable-layout--flush         : true;

/// @type Boolean [default] false - Produce CSS for layout items reversed?
$inuit-enable-layout--rev           : true;

/// @type Boolean [default] false - Produce CSS to align layout items to the vertical centers of each other?
$inuit-enable-layout--middle        : true;

/// @type Boolean [default] false - Produce CSS to align layout items to the vertical bottoms of each other?
$inuit-enable-layout--bottom        : true;

/// @type Boolean [default] false - Produce CSS to align layout items to all be the same height?
$inuit-enable-layout--full          : true;

/// @type Boolean [default] false - Produce CSS to make layout items fill up from the right hand side?
$inuit-enable-layout--right         : true;

/// @type Boolean [default] false - Produce CSS to make the layout items fill up from the center outward?
$inuit-enable-layout--center        : true;

/// @type Boolean [default] false - Produce CSS to make an individual layout item fill up the height of its layout?
$inuit-enable-layout__item--full    : true;

/// @type Boolean [default] false - Produce CSS to make an individual layout item fill up from the center vertically?
$inuit-enable-layout__item--center  : true;

/// @type Boolean [default] false - Produce CSS to make an individual layout item fill up from the bottom up?
$inuit-enable-layout__item--bottom  : true;




// PX-BOX-DESIGN
// configuration variables for px-box-design/_objects.box.scss
// documented in the px-box-design README.md:
// https://github.com/predixdev/px-box-design

/// @type Boolean [default] false - Produce CSS for boxes with no padding?
$inuit-enable-box--flush    : true;

/// @type Boolean [default] false - Produce CSS for boxes with tiny padding?
$inuit-enable-box--tiny     : true;

/// @type Boolean [default] false - Produce CSS for boxes with small padding?
$inuit-enable-box--small    : true;

/// @type Boolean [default] false - Produce CSS for boxes with large padding?
$inuit-enable-box--large    : true;

/// @type Boolean [default] false - Produce CSS for boxes with huge padding?
$inuit-enable-box--huge     : true;




// PX-FLAG-DESIGN
// configuration variables for px-flag-design/_objects.flag.scss
// documented in the px-flag-design README.md:
// https://github.com/predixdev/px-flag-design

/// @type Boolean [default] false - Produce CSS for flags with tiny gutters?
$inuit-enable-flag--tiny        : true;

/// @type Boolean [default] false - Produce CSS for flags with small gutters?
$inuit-enable-flag--small       : true;

/// @type Boolean [default] false - Produce CSS for flags with large gutters?
$inuit-enable-flag--large       : true;

/// @type Boolean [default] false - Produce CSS for flags with huge gutters?
$inuit-enable-flag--huge        : true;

/// @type Boolean [default] false - Produce CSS for flags with reversed content (i.e. image on right)?
$inuit-enable-flag--rev         : true;

/// @type Boolean [default] false - Produce CSS for flags with no gutters?
$inuit-enable-flag--flush       : true;

/// @type Boolean [default] false - Produce CSS for flags with top aligned content?
$inuit-enable-flag--top         : true;

/// @type Boolean [default] false - Produce CSS for flags with bottom aligned content?
$inuit-enable-flag--bottom      : true;

/// @type Boolean [default] false - Produce CSS for responsive flags?
$inuit-enable-flag--responsive  : true;




// PX-TOGGLE-DESIGN
// configuration variables for px-toggle-design/_objects.toggle.scss
// documented in the px-toggle-design README.md:
// https://github.com/predixdev/px-toggle-design

/// @type Boolean [default] false - Produce CSS for small toggles?
$inuit-enable-toggle--small     : true;

/// @type Boolean [default] false - Produce CSS for large toggles?
$inuit-enable-toggle--large     : true;

/// @type Boolean [default] false - Produce CSS for huge toggles?
$inuit-enable-toggle--huge      : true;




// PX-INPUT-GROUP-DESIGN
// configuration variables for px-input-group-design/_objects.input-group.scss
// documented in the px-input-group-design README.md:
// https://github.com/predixdev/px-input-group-design

/// @type Boolean [default] false - Produce CSS for one line form fields?
$inuit-enable-inline-fields                 : true;

/// @type Boolean [default] false - Produce CSS for bare text-inputs?
$inuit-enable-text-input--bare              : true;

/// @type Boolean [default] false - Produce CSS for field validation?
$inuit-enable-validation-states             : true;

/// @type Boolean [default] false - Produce CSS for tiny inputs?
$inuit-enable-input--tiny                   : true;

/// @type Boolean [default] false - Produce CSS for small inputs?
$inuit-enable-input--small                  : true;

/// @type Boolean [default] false - Produce CSS for regular inputs?
$inuit-enable-input--regular                : true;

/// @type Boolean [default] false - Produce CSS for large inputs?
$inuit-enable-input--large                  : true;

/// @type Boolean [default] false - Produce CSS for huge inputs?
$inuit-enable-input--huge                   : true;




// PX-SPACING-RESPONSIVE-DESIGN
// configuration variables for px-spacing-responsive-design/_trumps.spacing-responsive.scss
// documented in the px-input-group-design README.md:
// https://github.com/predixdev/px-spacing-responsive-design

/// @type Boolean [default] false - Produce CSS for default margin sizes?
$inuit-enable-margins                   : true;

/// @type Boolean [default] false - Produce CSS for tiny margin sizes?
$inuit-enable-margins--tiny             : true;

/// @type Boolean [default] false - Produce CSS for small margin sizes?
