/// @type Boolean [default] false - Produce CSS for tiny padding sizes?
$inuit-enable-paddings--tiny            : true;

/// @type Boolean [default] false - Produce CSS for small padding sizes?
$inuit-enable-paddings--small           : true;

/// @type Boolean [default] false - Produce CSS for large padding sizes?
$inuit-enable-paddings--large           : true;

/// @type Boolean [default] false - Produce CSS for huge padding sizes?
$inuit-enable-paddings--huge            : true;

/// @type Boolean [default] false - Produce CSS for swapping horizontal paddings in RTL?
$inuit-enable-paddings--rtl             : true;

/// @type Boolean [default] false - Produce CSS for zero paddings?
$inuit-enable-paddings--none            : true;

/// @type Boolean [default] false - Produce CSS for default margin sizes?
$inuit-enable-responsive-margins                    : true;

/// @type Boolean [default] false - Produce CSS for tiny margin sizes?
$inuit-enable-responsive-margins--tiny              : true;

/// @type Boolean [default] false - Produce CSS for small margin sizes?
$inuit-enable-responsive-margins--small             : true;

